.btn-primary {
    background-color: $primary-red;
    // background-image: linear-gradient(to right, $btn-primary-grad-1, $btn-primary-grad-2);
    border-radius: 2rem;
    font-family: 'Poppins';
    font-weight: 300;
    margin: 0 12px;
    border: none;
    display: inline-block;
    &:hover {
        background-image: linear-gradient(to right, $primary-red, $btn-primary-grad-1);
    }
}