

.line-hgt { 
    line-height: 1.6;
}

// Banner setcion 
.services-banner {

    // banner overlay
    &::after {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(255, 255, 255, 0.1);
        content: '';
        opacity: 1;
    }

    .services-bannerImg {
        position: absolute;
        left: 0;
        top: 0;
    }

    .services-bannerContent {
        z-index: 1;
        padding-top: 75px;
        max-width: 19.5rem;
        margin-left: 10%;

        .services-banner-title {
            position: relative;
            display: inline;
            font-size: 4.375rem;
            line-height: 1.14;
            letter-spacing: 1.12px;

            // red underline for heading
            &::after {
                position: absolute; 
                bottom: 0;
                left: 0;
                width: 105%;
                height: 1px;
                background-color: $primary-red;
                content: '';
                opacity: 1;
            }
        }

        .services-banner-subtitle {
            letter-spacing: 0.5px;
            position: relative;
            sup {
                font-size: 30px;
                top: 10px;
            }
        }
    }
}

// grid-row-spacing
.grid-row-spacing {
    padding: 3rem;
}

.grid-image {
    img {
        z-index: 2;
        box-shadow: 0px 3px 6px #00000029;
        max-height: 23.5rem;
        position: relative;
        -o-object-fit: cover;
        object-fit: cover;

    }

    .numbers-large {
        z-index: 1;
        top: -42%;
        left: -5%;
        font-size: 200px;
        opacity: 0.25;
    }

    .alt-numbers-large {
        z-index: 1;
        top: -42%;
        right: -5%;
        font-size: 200px;
        opacity: 0.25;
    }
}

.grid-text {
    .service-name {
        position: relative;
        max-width: 21rem;
        margin-left: 20px;

        .alternate {
            margin-left: 0px;
        }

        &::after {
            position: absolute;
            bottom: -25%;
            left: 0;
            width: 100%;
            height: 12px;
            background-color: $primary-red;
            content: '';
            opacity: 1;
        }
    }

    // alternate heading
    .alternate-name {
        .service-name {
            margin-left: 0;
        }
    }

    ul {
        color: $secondaryGray;
        font-weight: 500;
        font-size: 20px;
    }

    // alternate ul 
    .alternate-ul {
        ul {
            padding-left: 15px;
        }
    }
}