.all-blogs {
    // page title
    h1{ 
        font-size: 3rem;
        line-height: 3.44rem;
    }
    p{
        font-size: 16px;
    }
    .featured-blog {
        position: relative;
        .featured-img {
            position: absolute;
            top: 0;
            left: 0;
            padding:0 15px;
        }
        .blog-content {
            z-index: 2;
            position: relative;
           h5{
               font-size:2rem;
           }
        }
        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            margin: 0 15px;
            background-color: rgba(0, 0, 0, 0.5);
        }
    }
    .blog-img{
        box-shadow: 0px 3px 6px #00000029; 
        height: 200px;
      }
      h5{
          font-weight: 600;
          margin: 1.25rem 0 3px 0; 
      }
    .about-author {
        .author-img {
            width: 35px;
            height: 35px;
            border-radius: 50%;
            overflow: hidden;
            border: 2px solid $primary-red;
            margin-right: 6px;
        }
        span{
            font-size: 14px;
        }
    }
  
}