@media(min-width: 992px) {

  // spreading homepage/testimonials section/ h4 text
  .testimonials-section {
    .testimonials-carousel {
      .slick-list {
        .slick-track {
          .slick-center {

            >div {
              >div {
                >div {
                  // full-width {
                  width: 100vw;
                  position: relative;
                  left: 50%;
                  right: 50%;
                  margin-left: -50vw;
                  margin-right: -50vw;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media(max-width:1199px) {

  // home page
  // gallery-carousel section
  .gallery-carousel-section {
    right: 0;

    .label-gallery {
      left: 15px;
      bottom: 5%;
    }

    .label-organisation {
      right: 15px;
      top: 22%;
    }

  }

}

@media(min-width:1200px) {


  // services page
  // certifications
  .certifications {

    .row {
      margin-right: -45px;
      margin-left: -45px;
    }

    .col-lg-6 {
      padding-right: 45px;
      padding-left: 45px;
    }
  }
}

@media(max-width: 991px) {
  html {
    font-size: 13px;
  }

  // Global sass 
  .btn-primary {
    font-size: 13px;
  }

  .pop-up {
    .graphic-wrap {
      display: none;
    }
  }

  .navbar {
    padding: 16px 30px;
    top: 100px;

    &.zoom-in {
      padding: 5px 28px;
    }
  }

  //    Header section
  .navbar-img {
    width: 100px;
  }

  .navbar-hamburger-icon {
    width: 18px;
    margin-bottom: 7.3px;
  }

  .navbar-light .navbar-toggler {
    padding: 0 12px;
    margin-top: 8px;
  }

  //    banner section
  .bannerContent {
    .banner-title {
      font-size: 2.5rem;
    }
  }

  // Rubico Mission section
  .rubico-mission {


    p {
      font-size: 1.86rem;
      max-width: 540px;
      margin: -38px auto 0;
    }
  }

  //branches section
  .branches-section {

    .img-lg {
      max-height: 368px;

    }
  }

  .haridwar-section,
  .dehradun-section {
    h3 {
      text-align: center;
    }

    p {
      margin: 20px auto 26px auto;
    }
  }

  .dehradun-section {
    h3 {
      margin-left: 0;
    }

    p {
      margin-bottom: 0;
    }
  }

  //   Technologies section
  .technologies {
    .container {
      h2 {
        line-height: 1.2;

      }
    }
  }

  // Adjusting carousel arrow positions
  .openings-section,
  .testimonials-carousel {
    .slick-prev {
      left: 28px;
    }

    .slick-next {
      right: 28px;
    }
  }

  // gallery carousel
  .gallery-carousel-section {

    .slick-arrow {
      margin-top: 193px;
      padding: 5px 15px;

      &:before {
        font-size: 20px;
      }
    }

    .slick-prev {
      right: 41px;
    }

    .slick-next {
      right: 0;
    }
  }

  //    testimonials section
  .testimonials-section {
    .testimonials-carousel {
      .slick-slide {
        .overlay {
          img {
            width: 97.92px;
            height: 97.92px;
          }
        }
      }

      .slick-arrow {
        &:before {
          font-size: 44px;
        }
      }
    }
  }

  // Blogs section
  .blogs-section {
    h5 {
      margin-bottom: 0;
    }
  }

  // all blogs page - blog-page-2
  .all-blogs h5 {
    margin-bottom: 0;
  }

  // blogs inner page
  .popular-blogs .blogs-carousel h5 {
    margin-bottom: 0;
  }

  // about page
  .certifications {
    .text-cover {
      max-width: 270px;
    }
  }

  //   blogs-page-1
  .rubico-blog-1 {

    // blog banner img 
    .blogs-banner {
      .blog-author-img {
        width: 6rem;
        height: 6rem;
      }
    }

  }

  // inside popular blogs section
  .popular-blogs {

    // blogs-carousel
    .blogs-carousel {
      .slick-slide {
        padding: 0 20px;
      }
    }
  }

  // careers page outer
  .careers-section {
    .container {
      max-width: 720px;

      .location-btn {
        margin-right: 0px;
      }

      .card-filter {
        max-width: 250px;
        margin: auto;
        font-size: 16px;

        img {
          max-width: 24px;
        }


        .responsive-design {
          box-shadow: 7px 7px 11px #0000002B;
          border-radius: 17px;
          background-color: #fff;
          margin-bottom: 15px;

          // padding:0;
          &:nth-child(2) {
            padding: 0 15px !important;
          }
        }

        .search-field {

          min-width: 184px;

          &:after {
            font-size: 19px;
            right: 11px;
          }
        }

        .search-field,
        .tags,
        .dropdown-item {
          font-size: 16px;
        }

        .dropdown-menu {
          min-width: inherit;
        }


        // .search-for-job {
        //   padding: 3px 0 3px 10px;
        //   align-items: center;
        //   width: 100%;
        // }

        // .search-btn {
        //   padding: 5px 12px;
        //   font-size: 10px;
        // }

      }
    }
  }

  // careers page inner
  // form section
  .form-section {
    .form-template {
      .template-text {
        border-radius: 10px 10px 0 0;
      }

      .template-inputs {
        #custom-button {
          height: 31px;
        }
      }
    }
  }

  // // benefits-section
  .benefits-section .media {
    max-width: 330px;
  }

  //   services page
  // grid-row-spacing
  .grid-row-spacing {
    padding: 3rem 1.4rem;
  }

  // services page
  // grid-image
  .grid-image {

    // padding: 15px;
    .numbers-large {
      top: -48%;
      left: -6%;
    }

    .alt-numbers-large {
      top: -48%;
      right: -10%;
    }
  }

  // grid-text  
  .grid-text {
    ul {
      padding-left: 20px;
    }

    .service-name {
      margin-top: 20px;
      margin-left: 0;
      text-align: left;
      display: inline-block;

      &::after {
        bottom: -32%;
        left: 0;
        width: 100%;
        height: 15px;
      }
    }

    // alternate heading
    .alternate-name {
      .service-name {
        // margin-left: 15px;
      }
    }

    // alternate ul 
    .alternate-ul {
      ul {
        padding-left: 20px;
      }
    }
  }

  // Footer section
  .footer-section {
    .row {
      >div {
        text-align: center !important;
        margin-bottom: 2rem;
      }
    }

    .check-positions-btn {
      padding: 12px 5rem;
      margin: 0;
    }

    .get-in-touch {
      display: flex;
      flex-direction: column;

      .contact-info {
        margin: 0 auto;
        flex-direction: column;

        >div {
          margin: 20px 0;

          span {
            text-align: left;
          }
        }
      }

      h2 {
        font-size: 3rem;

        &:after {
          width: 60%;
          left: 50%;
          -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
        }
      }
    }
  }

  address {
    margin-bottom: 0;
  }

  .admin-office {
    margin-top: 32px;
  }

  // rubico-white-logo
  .rubico-white-logo {
    margin: 48px 0;
  }
}

@media(max-width:767px) {

  // responsive typography begins
  section {
    padding: 2rem 0;
  }

  // on the base of 13
  // h2- 24px-1.86rem
  // h3- 23px-1.77rem
  // h4- 22px-1.69rem
  // h5- 20px-1.53rem
  h1 {
    font-size: 2.5rem;
  }

  h2 {
    font-size: 1.86rem;
    margin: 0 auto 2rem;
    line-height: 1.2;
  }

  h3 {
    font-size: 1.77rem;
  }

  h4 {
    font-size: 1.69rem;
  }

  // responsive typography ends
  // pop-up banner
  .pop-up {
    display: block;
    padding: 0 0 6px;

    .announce-img {
      margin-right: 0;
      transform: none;
      -webkit-transform: none;
      -moz-transform: none;
      -ms-transform: none;
      -o-transform: none;
    }

    .announce-img+p {
      max-width: initial;
    }

    p,
    a {
      font-size: 1.2rem;
    }

    .close {
      top: 15px;
      right: 18px
    }
  }

  .navbar {
    top: 120px;
  }

  .nav-item a {
    font-size: 3.076rem;
  }

  // home page
  // Rubico Mission section
  .rubico-mission {
    padding-top: 4rem;

    h2 {
      max-width: 480px;
      font-size: 2.5rem;
    }

    p {
      font-size: 1.5rem;
      max-width: 448px;
      margin: -16px auto 0;

    }
  }

  // branches section
  .branches-section {
    .branch-name {
      font-size: 1.53rem;
      margin: 0;
    }

    .img-lg {
      height: 260px;

    }

    .img-sm>img:first-child {

      height: 147px;
    }

    .img-sm>img:last-child {

      height: 100px;
    }
  }


  .img-grid {


    >div {

      img {
        max-height: 100px;
      }
    }



  }

  .haridwar-section {

    img {
      max-height: 147px;
    }
  }

  // openings carousel arrows
  .openings-section .openings-carousel .slick-arrow:before {
    font-size: 20px;
  }

  // gallery carousel
  .gallery-carousel-section {
    h2 {
      margin: 0 auto 4rem;
    }

    .label {
      font-size: 1rem;

    }

    .label-gallery {
      bottom: 2%;
      padding: 8px 24px;
    }

    .label-organisation {
      top: 20%;
      max-width: none;
      padding: 10px 24px;
    }
  }


  // testimonials arrows resize
  .testimonials-section .testimonials-carousel {
    .slick-arrow:before {
      font-size: 35px;
    }
  }

  // careers page outer
  .careers-section {
    .container {
      padding-top: 2rem;
    }
  }

  //   careers form page
  .careers-banner {

    // banner overlay
    &::after {
      background-color: rgba(0, 0, 0, 0.52);
      opacity: .5;
    }

    .careers-bannerImg {
      -o-object-position: right;
      object-position: right;
    }

    .careers-bannerContent {
      margin-top: auto;
      padding: 0 0 75px;

      .careers-banner-title {
        font-size: 3.85rem;
      }

      .careers-banner-title,
      .careers-banner-subtitle {
        color: #fff !important;
      }

    }
  }

  // details about the job
  .job-profile {

    //  max-width: 1120px;
    margin: 52px 15px 26px;
    padding: 11px;
    box-shadow: 7px 7px 11px #0000002B;
  }

  // form section
  .form-section {
    .form-template {
      .template-text {
        line-height: 1.2;

        p {
          margin: 5px 0;
        }

        .form-img {
          margin-top: 52px;
        }
      }

      .template-inputs {

        padding: 52px 15px 35px;

        .submit-btn {
          button {
            margin: 0;
          }
        }
      }
    }
  }

  //all blogs page
  .all-blogs {

    // page title 
    h1 {
      font-size: 1.86rem;
      line-height: 1.2;
    }
  }

  // blogs page-1
  .rubico-blog-1 {

    // blog-title
    .blog-title {

      font-size: 1.86rem;
      line-height: 1.2;

    }

    // Blog Share dropdown
    .dropdown {

      // dropdown menu begins
      .dropdown-menu {
        padding: 1rem 0;
        left: 4.1rem !important;
        min-width: 5.5rem !important;
      }
    }

    // blog banner img 
    .blogs-banner {
      .blog-author-img {
        width: 5rem;
        height: 5rem;
        left: 44%;
      }
    }

    //   removing left red seperation line
    .sidebar:after {
      width: 0;
    }
  }

  // inside popular blogs section
  .popular-blogs {

    // blogs-carousel
    .slick-arrow:before {
      font-size: 24px;
    }

    .slick-prev {
      left: -15px;
    }

    .slick-next {
      right: -15px;
    }
  }

  // about page
  .about-banner {

    // banner overlay
    &::after {
      background-color: rgba(0, 0, 0, 0.52);
      opacity: .5;
    }

    .about-bannerImg {
      -o-object-position: right;
      object-position: right;
    }

    .about-bannerContent {

      .about-banner-title,
      .about-banner-subtitle {
        color: #fff !important;
      }
    }
  }

  // benefits-section
  .benefits-section .media {
    max-width: 270px;
  }

  // certifications section
  .certifications {
    .icon-cover {
      max-width: 130px;
      height: 130px;
      padding: 1rem;
    }
  }

  // advantages-section
  .advantages-section {

    .spacing {
      margin: 2rem 0;
    }
  }

  //when spacing is required more than default classes
  .spacing {
    margin: 2rem 0 0;
  }

  // services page
  .services-banner {

    // banner overlay
    &::after {
      background-color: rgba(0, 0, 0, 0.52);
      opacity: .5;
    }

    .services-bannerImg {
      -o-object-position: right;
      object-position: right;
    }

    .services-bannerContent {

      .services-banner-title,
      .services-banner-subtitle {
        color: #fff !important;
      }
    }
  }
  // newsletter archive page
  .archives-main-banner {

    // banner overlay
    &::after {
      background-color: rgba(0, 0, 0, 0.52);
      opacity: .5;
    }

    .newsletter-bannerContent {
    .newsletter-banner-title,
      .newsletter-banner-subtitle {
        color: #fff !important;
      }
    }
  }
  .grid-image {

    .numbers-large,
    .alt-numbers-large {
      font-size: 160px;
    }
  }

  .grid-text .service-name {
    max-width: none;

    &::after {
      height: 5px;
      bottom: -28%;
    }
  }

  .rc-banner-content {
    h1 {
      font-size: 3.5rem;
    }
  }

  .rc-steps {
    text-align: center !important;
   .m-md-0{
     margin-bottom: 4rem !important;
   }
  }

  // rubico charter
  .rubico-charter {
    h6 {
      margin-top: 0;
    }

    p {
      margin-bottom: 2.4rem;
    }
  }

  .charter-grid {
    display: block;
  }
}

@media(min-width: 992px) {

  // pop-up banner
  .pop-up {
    min-height: 120px;
    -webkit-box-pack: space-evenly;
    justify-content: space-evenly;
  }

  // footer-section
  .social-link-wrap {
    position: relative;
    top: -6rem;
  }

  .footer-section {
    .get-in-touch {
      h2 {
        margin: 0;
      }
    }
  }
}

@media(max-width:575px) {
  .popup-banner-modal {

    // display: block;
    &.in {
      display: -webkit-box;
      display: flex;
      -webkit-box-pack: center;
      justify-content: center;
      align-items: center;
    }

    .modal-dialog {
      margin: 0.5rem auto;
    }
  }

  // navbar
  .navbar {
    top: 138px;
  }

  // home page
  // Rubico Mission section
  .rubico-mission {
    h2 {
      font-size: 2rem;
    }
  }

  //   Technologies section
  .technologies {
    .container {
      .col-sm-6:last-child {
        margin-bottom: 1rem !important;
      }
    }
  }

  .testimonials-section .testimonials-carousel .slick-slide .overlay {
    min-height: 31.73rem;
  }

  // careers banner
  .careers-section {
    .container {
      padding-top: 0 !important;

      .col-sm-6:last-child {
        margin-bottom: 0 !important;
      }
    }
  }

  //   blogs-page-1
  // inside rubico-blog-1 section
  .rubico-blog-1 {

    // Blog Share dropdown
    .dropdown {

      // dropdown menu begins
      .dropdown-menu {
        padding: 1rem 0;
        left: 5rem !important;
        min-width: 4.8rem !important;
        padding: 12px 0 !important;
      }
    }

    .blogs-banner {
      .blog-author-img {
        width: 4rem;
        height: 4rem;
        left: 44%;
      }
    }

  }

  //   services page
  .grid-image {
    img {
      margin: 12px 0;
    }

    .numbers-large {
      top: -32%;
      left: -3%;
      font-size: 120px;
      transform: translate(-3%, -3%);

    }

    .alt-numbers-large {
      top: -32%;
      right: -6%;
      font-size: 120px;
      transform: translate(3%, -3%);

    }
  }
}
