body {
    font-family: 'Poppins', sans-serif; 
    -webkit-font-smoothing: antialiased;
    font-size: 1rem; 
}

h1 {
    font-weight: 700;
    line-height: 4.5rem;
    font-size: 4.375rem;
}

h2 {
    font-size: 3rem;
    margin: 0 auto 4rem;
    font-weight: 600;
    max-width: 401px;
    line-height: 3.44rem;
    color: #767272;
}

h3 {
    font-size: 2.5rem;
    line-height: 3.125rem;
    letter-spacing: 0.5px;
}

h4 {
    font-size: 1.875rem;
    line-height: 2.5rem;
}

h5 {
    font-size: 1.5rem;
    line-height: 1.875rem;
}

h6 {
    font-size: 1.125rem;
    line-height: 1.375rem;
}

p {
    font-size: 1rem;
}

h2,
h3,
h4 {
    font-weight: 600;
}

h1,
p {
    margin-top: 10px;
    margin-bottom: 10px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Poppins', sans-serif;
}

a {
    text-decoration: none;
    display: inline-block;
    &:hover {
        text-decoration: none;
        color: inherit;
    }
    &:focus {
        outline: none;
    }
}