body, #bodyTable, #templatePreheader, .spacer, #templateFooter{
    background-color: unset !important; 
}
.newsletter-bg{
    background-image: url("/wp-content/uploads/2020/05/bannerImage-Final.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -1;
    background-attachment: fixed;
}
.newsletter-bg::after{
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    opacity: 0.5;
    background-color: rgb(0, 0,0);
    z-index: -1;
}