// recruitment page banner image
.rc-banner-img {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.recruitment-banner:after {
  position: absolute;
  content: " ";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.18);
}
// recruitment page banner content
.rc-banner-content {
  z-index: 1;
  padding-top: 27rem;

  h1{
    letter-spacing: 1.12px;
  }
p{
    font-size: 1.5rem;
    line-height: 2.5rem;
    max-width: 34.33rem;
    color: #fff;
    margin:auto;
 }
}

//process-intro
.process-intro p{
    color: #767272;
    max-width: 34.33rem;
    font-size: 1.125rem;
    font-weight: 500;
    margin: 0 auto;
}
// recruitment steps
.rc-steps{
    h3{
        font-size: 24px;
        letter-spacing: 0;
        font-weight: 700;
    }
    h4, p{
    font-size: 18px;
    font-weight: 400;
    line-height: 1.7;
    }
    p{
        color: #979797;
    }
    .step-num{
        font-weight: 600;
        line-height: 1.2;
    }
    img{
        max-width: 20.3rem;
        margin:0 auto;
    }
}

// rubico charter
.rubico-charter h6{
    max-width: 54rem;
    font-size: 1.25rem;
    margin: 3.75rem auto;
}
.rubico-charter img{
   max-height: 25rem;
   min-height: 20rem;
    object-fit: cover;
    margin-bottom: 1.875rem;
    box-shadow: 0 3px 6px rgba(0,0,0,.16);
}
.rubico-charter h5{
    font-weight: 600;
    font-size: 1.25rem
}
.rubico-charter p {
    margin-bottom: 4.8rem;
    margin-top: 0;
    font-size: 18px;
    font-weight: 400;
    line-height: 1.7;
}

.process-intro p,.rubico-charter h6 {
  line-height: 1.5;
}
// browse more positions
.more-positions h2{
    font-size: 2rem;
}
.more-positions p{
    max-width: 24.4rem;
    margin: auto;
}

.charter-grid{
    display: grid;
    grid-gap:0px 45px;
    grid-template-columns: 350px auto;
    // grid-auto-flow: row dense;
}
.item-1{
    grid-row: 1/ span 3;
}

// <!-- FAQ section -->
.faq-sec{
    .card-header{
        padding: 0;
        margin-bottom: 0 !important;
        background-color: transparent !important;
        border-bottom: 1px solid $light-shadow;;
    }

    .card {
        border: transparent !important;
        border-bottom: 1px solid $light-shadow;;

    }

    a[data-toggle="collapse"]{
        position: relative;
        display: block;
        font-size: 1.188rem;
        color: #000 !important; 
        cursor: pointer;
        padding: 0.625rem;
        padding-right: 35px;
    }

    a[data-toggle="collapse"]::after{
        content: "\f078"; 
        position: absolute;
        font-family: "Font Awesome 5 Free";
        right:14px;
        position:absolute;
        top:50%;
        font-weight:600;
        transform: translateY(-50%);
        color:$light-shadow;
        transition: all .6s ease;


    }

    a[aria-expanded="true"]{
        color: $primary-red !important;
    }
    
    a[aria-expanded="true"]::after{
        color: $primary-red !important;
        transform: translateY(-50%) rotate(-180deg);
    }
}
// <!-- FAQ section -->