.pop-up{
    background-color: #000;
    display: -webkit-box;
    display: flex;
    padding: 20px 0;
    -webkit-box-pack: center;
            justify-content: center;
    -webkit-box-align: center;
            align-items: center;
    width: 100%;
    z-index: 3;
    color: #fff;
    position: absolute;
    top: 0;
    -webkit-animation: bounce-in-top .7s both;
    animation: bounce-in-top .7s both;
    &.hide{
        display: none !important;
    }
    // animation keyframes
    @-webkit-keyframes bounce-in-top {
        0% {
            -webkit-transform: translateY(-250px);
            transform: translateY(-250px);
            -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
            opacity: 0;
            -moz-transform: translateY(-250px);
            -ms-transform: translateY(-250px);
            -o-transform: translateY(-250px);
        }
    
        38% {
            -webkit-transform: translateY(0); 
            transform: translateY(0);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
            opacity: 1;
        }
    
        55% {
            -webkit-transform: translateY(-65px);
            transform: translateY(-65px);
            -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
        }
    
        72% {
            -webkit-transform: translateY(0);
            transform: translateY(0);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
        }
    
        81% {
            -webkit-transform: translateY(-28px);
            transform: translateY(-28px);
            -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
        }
    
        90% {
            -webkit-transform: translateY(0);
            transform: translateY(0);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
        }
    
        95% {
            -webkit-transform: translateY(-8px);
            transform: translateY(-8px);
            -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
        }
    
        100% {
            -webkit-transform: translateY(0);
            transform: translateY(0);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
        }
    }
    
    @keyframes bounce-in-top {
        0% {
            -webkit-transform: translateY(-250px);
            transform: translateY(-250px);
            -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
            opacity: 0;
            -moz-transform: translateY(-250px);
            -ms-transform: translateY(-250px);
            -o-transform: translateY(-250px);
        }
    
        38% {
            -webkit-transform: translateY(0);
            transform: translateY(0);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
            opacity: 1;
        }
    
        55% {
            -webkit-transform: translateY(-65px);
            transform: translateY(-65px);
            -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
        }
    
        72% {
            -webkit-transform: translateY(0);
            transform: translateY(0);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
        }
    
        81% {
            -webkit-transform: translateY(-28px);
            transform: translateY(-28px);
            -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
        }
    
        90% {
            -webkit-transform: translateY(0);
            transform: translateY(0);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
        }
    
        95% {
            -webkit-transform: translateY(-8px);
            transform: translateY(-8px);
            -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
        }
    
        100% {
            -webkit-transform: translateY(0);
            transform: translateY(0);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
        }
    }
    .announce-img{
    width: 50px;
    height: 40px;
    -o-object-fit: contain;
       object-fit: contain;
    margin-right: 3.625rem;
    -webkit-transform: scale(1.9);
            transform: scale(1.9);
    }
    .announce-img + p {
        margin:0 2rem;
        font-size: 1.5rem;
        max-width: 39.25rem;
    }
    a{
    border-bottom: 1px solid $primary-red;
    margin-left: 1.25rem;
    font-weight: 700;
    color: $primary-red;
    font-size: 1.5rem;
    }
    .graphic-wrap{
    margin-left: 30px;
    position: relative;
    img {
        width: 50px;
        position: absolute;
    }
    img:nth-of-type(1) {
            bottom: -20px;
        }
        img:nth-of-type(2) {
        width: 20px;
        top: -20px;
        left: 130px;
        }
        img:nth-of-type(3) {
        right: -245px;
        bottom: -8px;
        }  
       
   }
    .close{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 30px;
    color: #fff;
    font-size: 2rem;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    }
 } 

.navbar {
    padding: 20px 60px;
    width: 100%;
    background: #fff;
    -webkit-transition: padding .1s ease;
    transition: padding .1s ease;
    top: 120px;
    &.slide-up{
        top:0 !important;
    }
    &.zoom-in {
        padding: 5px 60px;
    }
}

.navbar-brand {
    z-index: 2;
}

.navbar-img {
    width: 120px;
}

.navbar-hamburger-icon {
    width: 22.63px;
    height: 2px;
    background-color: $primary-red;
    margin-bottom: 10.3px;
    display: block;
}

.btn-apply {
    width: 124px;
    height: 40px;
}

// sliding black background of hamburger menu
.navbar-collapse {
    position: fixed;
    top: 120px;
    left: 0;
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
    background-color: black;
    width: 100%;
    height: 100%;
    -webkit-transition: 0.2s ease;
    transition: 0.2s ease;
    z-index: 1; 
    display: -webkit-box;
    display: flex;
    -webkit-box-pack: center;
    justify-content: center;
    
    .nav-link{
        display: inline;
    }
    &.slide-up{
        top: 0;
    }
    &.show {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
}

// navbar button animation
.navbar-toggler {
    border: none;
    display: block;
    margin-top: 10px;
    z-index: 2;
    &:focus {
        outline: none;
    }
    &.show {
        .navbar-hamburger-icon {
            background-color: #fff;
        }
        &.show:hover {
            .navbar-hamburger-icon {
                background-color: $primary-red;
            }
        }
    }
    .top-bar {
        -webkit-transform: rotate(0);
        transform: rotate(0);
        -webkit-transition: 0.01s ease;
        transition: 0.01s ease;
        &.show {
            -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
            -webkit-transform-origin: 10% 10%;
            transform-origin: 10% 10%;
        }
    }
    .middle-bar.show {
        display: none;
    }
    .bottom-bar {
        -webkit-transform: rotate(0);
        transform: rotate(0);
        -webkit-transition: 0.2s ease;
        transition: 0.2s ease;
        &.show {
            -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
            -webkit-transform-origin: 10% 90%;
            transform-origin: 10% 90%;
        }
    }
}

.nav-item {
    a {
        font-size: 48px;
        color: #ffffff;
        font-weight: 200;
        letter-spacing: 1px;
        width: 60px;
      cursor: unset;
      &:hover{
        color: $primary-red;
        cursor: pointer;
        }
    }
   
}

// color change for social media icons on hover
.social-links {
    color: #ffffff;
    display: inline;
    font-size: 40px;
    // margin-right: 10px;
    // padding: 8px;
    cursor: pointer;
    &:hover {
        color: $primary-red;
    }
} 

.navbar-nav {
    padding: 5% 20% 5% 0;
}