// removing h2 global max-width
.section-heading {
    max-width: initial;
} 

// general spacing class
.spacing {
    margin: 3.75rem auto;
}

.line-hgt {
    line-height: 1.6;
}

// Banner setcion 
.about-banner {
    // banner overlay
    &::after {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(255, 255, 255, 0.1);
        content: '';
        opacity: 1;
    }

    .about-bannerImg {
        position: absolute;
        left: 0;
        top: 0;
    }

    .about-bannerContent {
        z-index: 1;
        padding-top: 75px;
        max-width: 22.5rem;
        margin-left: 10%;

        .about-banner-title {
            position: relative; 
            display: inline;
            font-size: 4.375rem;
            line-height: 1.14;
            letter-spacing: 1.12px;

            // red underline for heading
            &::after {
                position: absolute;
                bottom: 0;
                left: 0;
                width: 105%;
                height: 1px;
                background-color: $primary-red;
                content: '';
                opacity: 1;
            }
        }

        .about-banner-subtitle {
            max-width: 280px;
            letter-spacing: 0.5px;
             position: relative;
            sup {
                font-size: 30px;
                top: 10px;
            }
        }
    }
}
// benefits-section
.benefits-section{
    padding-bottom: 2.5rem;

    img {
       width: 2.5rem;
       height: 2.5rem; 
       margin-right: 2rem;
    }
    .media{
        max-width: 390px; 
        margin: auto;
    }
    .media-body{
        text-align: left; 
    }
}
// certifications
.certifications {
    padding-bottom: 2.5rem;
    
    .icon-cover{
        width: 100%;
        max-width: 180px;
        height: 180px;
        background-color: #fff;
        border-radius: 17px;
        box-shadow: 0px 3px 6px #00000029;
        padding: 2rem;
        // margin:0 30px 30px 0 !important;
        img{
        object-fit: contain;   
    }
    
}
    .text-cover{
        max-width: 320px;
       
    
        h5{
            font-size: 20px;
            margin:0;
            font-weight: 400;
        }
    }
}

// advantages-section
.advantages-section {
    img {
        box-shadow: 0px 3px 6px #00000029;
    }
    h2+h6{
        font-weight:500;
    }
    h6{
        font-weight: 600;
    }
}
//recruitment section
.about-recruitment{
   h2{
    font-weight:700;
    font-size: 2rem;
   }
   h6{
       line-height: 1.5;
   }
}
// golden rule section
.golden-rule-section {
    height: 330px;

    img {
        position: absolute;
        top: 0;
        left: 0;
    }

    .golden-rule {
        padding-top: 100px;
        z-index: 2;
        position: relative;
    }

    // img black overlay 
    &::after {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0, 0, 0, 0.6);
        content: '';
        opacity: 1;
    }
}


