    .footer-section {
        // footer background color
        background-color: #000; 
        color: #fff;
        // get in touch heading
        .get-in-touch {
            margin-bottom: 5rem;
            h2 {
                position: relative;
                font-size: 3.56rem;
                line-height: 2;
                font-weight: 700;
                margin: 0 auto;
                // heading underline
                &::after {
                    content: "";
                    position: absolute;
                    width: 75%;
                    height: 5px;
                    background-color: #fff;
                    bottom: 0;
                    left: 0;
                }
            }
            .contact-info{
                div{
                    margin-right: 40px;
                }
                 
            img{
                margin-right: 10px;
            }
                h6{
                margin: 0;
                line-height:0.8;
                font-size: 13px;
            }
            a{
                font-size: 13px;
                line-height:0.8;
            }
        }
        }
        // rubico-white-logo
        .rubico-white-logo {
            width: 150px;
            margin-top: 2.5rem;
        }
        h6 {
            line-height: 1.5;
            font-weight: 700;
            font-size: 15px;
        }
        // address tag
        address {
            font-size: 15px;
            font-weight: 300;
            // max-width: 11.25rem;
        }
        .admin-office {
            .view-map,
            h6,
            address {
                font-size: 12px;
            }
        }
        // maps link
        .view-map {
            color: #fff;
            border-bottom: 2px solid transparent;
            h6 {
                margin: 0;
            }
            &:hover {
                border-bottom: 2px solid #fff;
            }
        }
        .check-positions-btn {
            margin: 0 0 48px;
            padding: 8px 32px;
            -webkit-box-ordinal-group: 6;
                    order: 5;
        }
        .copy-right {
            p {
                font-size: 12px;
                margin-top: 20px;
            }
        }
        .footer-social-links {
            li {
                display: inline-block;
                a{
                    width: 60px;
                    cursor: initial;
                }
            }
            
        }
    }