html {
    box-sizing: border-box;
    font-size: 16px;
} 
main { 
    margin-top: 6.125rem;
  }

.o-cover {
    -o-object-fit: cover;
    object-fit: cover;
}

section {
    padding: 4rem 0;
}
//when spacing is required more than default classes
.spacing {
    margin: 3.75rem 0;
} 
.text-gray-1 {
    color: $primaryGray;
}

.text-gray-2 {
    color: $secondaryGray;
}

.text-red {
    color: $primary-red;
}

.bg-pink {
    background-color: $light-pink;
}

.banner {
    position: relative;
    min-height: calc(100vh - 6.125rem);
}
.card-shadow {
    box-shadow: 7px 7px 11px $light-shadow;
}

.label {
    background-color: $primary-red;
    color: #fff;
    padding: 10px 1.875rem;
    display: inline-block;
    font-size: 1.5rem;
    letter-spacing: 1px;
}

.slick-next:before,
.slick-prev:before {
    color: $primary-red;
}

.slick-slide:focus {
    outline: none;
}

:focus {
    outline: -webkit-focus-ring-color auto 3px;
}

.btn-primary.focus,
.btn-primary:focus,
.btn-primary:active {
    box-shadow: 0 0 0 0.13rem rgba(38, 143, 255, .5);
    background-color: $primary-red;
}