.archives-main-banner {
    overflow: hidden;
  &::after {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(255, 255, 255, 0.1);
    content: '';
  }
  }
   .newsletter-bannerContent {
        z-index: 1;
        padding-top: 75px;
        max-width: 19.5rem;
        margin-left: 10%;

        .newsletter-banner-title {
            position: relative;
            display: inline;
            font-size: 4.375rem;
            line-height: 1.14;
            letter-spacing: 1.12px;

            // red underline for heading
            &::after {
                position: absolute; 
                bottom: 0;
                left: 0;
                width: 105%;
                height: 1px;
                background-color: $primary-red;
                content: '';
                opacity: 1;
            }
        }

        .newsletter-banner-subtitle {
            letter-spacing: 0.5px;
            position: relative;
            sup {
                font-size: 30px;
                top: 10px;
            }
        }
    }
  .archives-section {
    .container {
      max-width: 992px;
      padding-top: 3rem;
      h5 {
        font-weight: 500;
      } 
      .location-dropdown{
        display: none;
      }
      .location-btn{
       max-width: 250px;
       margin-right: 30px;   
      }
      // card-filter
      .card-filter {
        box-shadow: 7px 7px 11px #0000002B;
        border-radius: 17px;
        border: 0;
        padding: 15px;
        background-color: #fff;
        color: rgba(80, 93, 107,0.5);
        font-size: 1.25rem;
        font-weight: 500;
        
        &:hover {
            color: rgba(80, 93, 107,1);
            img{
              opacity: 1;
            }
          }
        img{
          max-width:30px;
          opacity: 0.5;
         
        }
        .black-border {
          position: relative;
        }
        button:focus{
          outline: none; 
        }
        .search-field {
          background-color: #fff;
          outline: none;
          color: rgba(80, 93, 107,0.5);
          font-size: 1.25rem;
          border: none;
          font-weight: 500;
          border-radius: 0;
          align-items: center;
          padding: 0 50px 0 0;
          min-width: 282px;
          &:hover {
            color: rgba(80, 93, 107,1);
          }
          &::after {
            color: $primary-red;
            font-size: 25px;
            font-family: Font Awesome\ 5 Free;
            content: "\f053";
            font-weight: 900;
            width: auto;
            height: auto;
            z-index: 1;
            border: none;
            position: absolute;
            right: 0px;
            top: 50%;
            transform: translateY(-50%) rotate(-90deg);
          }
  
        }
        
        .dropdown-menu {
          box-shadow: 7px 7px 11px #0000002B;
          border-radius:17px;
          padding: 15px 0;
          border: none;
          // margin-left: -15px;
          margin-top: 33px;
          
          max-height: 16.5rem;
          overflow-y:auto;
          overflow-x:hidden;
          min-width: 282px;
          left:-15px !important;
          .dropdown-content {
            height: 220px;
            overflow: auto;
            &::-webkit-scrollbar{
              background:#f8f9fa; // manage scrollbar background color here
              width: 5px;
              display: none;
              border-radius: 17px;
            }
            &::-webkit-scrollbar-thumb {
              background:rgba(80,93,107,0.4);
              height: 5px !important;
              border-radius: 17px;
              display:none;
              -webkit-transition: all .5s ease;
              transition: all .5s ease;
              }
              &::-webkit-scrollbar-thumb:hover {
                
                opacity: 1;
              }
              &:hover{
                cursor: pointer;
                &::-webkit-scrollbar-thumb, &::-webkit-scrollbar{
                  display: block;
                }
              
              }
            }
  
          .dropdown-item {
            padding: .25rem 15px;
            font-size: 1.25rem;
            color: rgba(80, 93, 107,0.5);
            overflow: hidden;
            font-weight: 500;
  
            &:hover {
              background: rgba(118, 114, 114, 0.15);
              color: rgba(80, 93, 107,1);
              box-shadow: 7px 7px 11px #0000002B;
            }
          }
        }
  
        .search-for-job {
          padding-right:15px;
          align-items: center;
        }
  
        .search-btn {
          padding: 8px 30px;
        }
  
        .btn-secondary:focus {
          box-shadow: none;
        }
  
        .tags {
          border: none;
          box-shadow: none;
          padding:0 5px 0 0;
          color: #505D6B;
          font-size: 1.25rem;
          opacity: 0.5;
          background-color: #fff;
          outline: none;
          color: #505d6b;
          font-size: 1.25rem;
          border-radius: 0;
  
          &:after {
            display: none;
          }
  
          &:hover {
            opacity: 1;
          }
        }
      }
      
  //  cards inside technologies section 
      .card {
        max-width: 300px;
        margin-left: auto;
        margin-right: auto;
        border:1px solid #fff;
        border-radius: 17px;
        border-bottom: 10px solid $primary-red;
        -webkit-transition: 0.2s linear;
        transition: 0.2s linear;
  
        &:hover {
          -webkit-transform: scale(1.08);
          transform: scale(1.08);
  
        }
      }
  
      .card-body {
        padding: 20px;
  
        h4 {
          font-weight: 400;
  
          span {
            font-weight: 600;
          }
        }
  
        img {
          border-radius: 10px;
          height: 18rem;
          margin-bottom: 12px;
          object-fit: contain;
        }
      }
  
      .card-title {
        color: #505D6A;
        font-weight: 600;
      }
    }
  }
  