.rubico-blog-1 {

    // blog-title
    .blog-title { 
        font-size: 3rem;
        line-height: 3.44rem;
    }
 
    //about author and date of publishing 
    .blog-author {
        p {
            font-weight: 500;
        }
    }

    // Blog Share dropdown 
    .dropdown {

        // dropdown button 
        .dropdown-toggle {
            font-weight: 500;
            font-size: 1.1rem;

            .fa-share-alt {
                font-size: 1.5rem;
                margin-right: 1rem;
            }

            &::after {
                display: none;
            }

            &:focus {
                box-shadow: none;
            }
        }

        // dropdown menu begins
        .dropdown-menu {
            .share {
                font-size: 1rem;
            }

            left: 3.6rem !important;
            border: none;
            border-radius: 17px;
            box-shadow: 7px 7px 11px #0000002B;
            min-width: 6rem !important;
            padding: 1.5rem 0;

            .dropdown-item {
                color: $primaryGray;
                font-size: 1.3rem;
            }

        }
    }

    // banner image
    .blogs-banner {
        

        .blog-author-img {
            width: 6.875rem;
            height: 6.875rem;
            left: 45%;
            -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
            border: 2px solid $primary-red;
            border-radius: 50%;
        }
    }

    &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 50%;
        width: 40%;
        height: 1px;
        -webkit-transform: translateX(-50%);
        transform: translateX(-50%);
        background-color: $primary-red;
    }
.blog-text{
    h1,h2,h3,h4,h5,h6{
        display: block;
        margin-left: 0;
        margin-right: 0;
        font-weight: bold;
        color: unset;
        max-width:unset;
    }
    h1{
        font-size: 2em;
        margin-top: 0.67em;
        margin-bottom: 0.67em;
    }
    h2{
        font-size: 1.5em;
        margin-top: 0.83em;
        margin-bottom: 0.83em;
        }
        h3{
            font-size: 1.17em;
margin-top: 1em;
margin-bottom: 1em;
        }
        h4{
            font-size: unset;
            margin-top: 1.33em;
            margin-bottom: 1.33em; 
        }
        h5{
            font-size: .83em;
            margin-top: 1.67em;
            margin-bottom: 1.67em;  
        }
        h6{
            font-size: .67em;
            margin-top: 2.33em;
            margin-bottom: 2.33em; 
        }
}
    .sidebar {
        sup {
            font-size: 30px;
            top: 10px;
        }

        &>p {
            margin-bottom: 48px;
        }

        // red line left of sidebar
        &:after {
            content: '';
            position: absolute;
            width: 1px;
            height: 100%;
            background-color: $primary-red;
            top: -10%;
            left: -10%;
        }
    }
    .advertisement{
        a{
            max-width: 220px;
            margin: 0 auto;  
        }
    }

}

.popular-blogs {
    >a {
        font-size: 12px;
    }

    .blogs-carousel {
        img {
            display: inline-block;
        }

        h5 {
            font-weight: 600;
            margin: 1.25rem 0 3px 0;
        }

        .blog-img {
            height: 200px;
        }

        p {
            display: inline-block;
            font-size: 16px;
        }

        .slick-list {
            display: -webkit-box;
            display: flex;

            .slick-track {
                display: -webkit-box;
                display: flex;

                .slick-slide {
                    padding: 0 12px;

                    >div {
                        height: 100%;

                        >div {
                            height: 100%;
                            display: -webkit-box !important;
                            display: flex !important;
                            -webkit-box-orient: vertical;
                            -webkit-box-direction: normal;
                                    flex-direction: column;
                        }
                    }
                }
            }
        }
    }

    // carousel button's content
    .slick-arrow {
        font-weight: 900;
        width: auto;
        height: auto;
        z-index: 1;

        &:before {
            font-family: Font Awesome\ 5 Free;
            font-size: 35px;
        }
    }

    // prev button
    .slick-prev {
        left: -32px;
        &:before {
            content: "\f053";
        }
    }

    // next button
    .slick-next {
        right: -32px;
        &:before {
            content: "\f054";
        }
    }

    .about-author {
        .author-img {
            width: 35px;
            height: 35px;
            border-radius: 50%;
            overflow: hidden;
            border: 2px solid $primary-red;
            margin-right: 6px;
        }
        span{
            font-size: 14px;
        }
    }
}