 // Banner setcion
 .careers-banner {
     position: relative;
     display: -webkit-box;
     min-height: calc(100vh - 98px);

     // banner overlay
     &::after {
         position: absolute;
         top: 0;
         left: 0;
         right: 0;
         bottom: 0;
         background-color: rgba(0, 0, 0, 0.12);
         content: '';
         opacity: .01;
     }

     .careers-bannerImg {
         position: absolute;
         left: 0;
         top: 0;
     }

     .careers-bannerContent {
         z-index: 1;
         position: relative;
         padding-top: 75px;
         max-width: 40.25rem;
         margin-left: 10%;

         .careers-banner-title {
             display: inline;
             font-size: 4.375rem;
             line-height: 1.14;
             letter-spacing: 1.12px;
         }

         .careers-banner-subtitle {
             margin: 30px 0;
             letter-spacing: 0.5px;
             max-width: 460px;
         }
     }
 }

 // details about the job 
 .job-profile {
     border-radius: 17px;
     max-width: 1120px;
     margin: 130px auto 50px;
     padding: 45px;
     
     ul { 
         color: $primaryGray;
         list-style-type: none;
         padding: 0;
         
         li {
             color: $secondaryGray;
             padding-left: 5px;
             position: relative;
             .dot{
                position: absolute;
                left:-5px;
                top:7px;
                border-radius: 50%;
                width:5px;
                height: 5px;
                background-color: red;
               }
          &::before{
            //   color: red;
            //   content: "\2022";
            //   font-weight: bold;
            //   padding-right: 5px;
          }
            }
     }

     h2 {
         font-weight: 400;
     }



     .job-highlights {
         font-size: 1.125rem;

         i {
             font-size: 1.56rem;
         }

         // Blog Share dropdown
    .dropdown {

        // dropdown button
        .dropdown-toggle {
            font-weight: 500;
            font-size: 1.1rem;

            .fa-share-alt {
                font-size: 1.5rem;
                margin-right: 1rem;
            }

            &::after {
                display: none;
            }

            &:focus {
                box-shadow: none;
            }
        }

        // dropdown menu begins
        .dropdown-menu {
            .share {
                font-size: 1rem;
                &:hover{
                    background-color: #fff;
                    cursor: unset;
                }
            }

            left: 3.6rem !important;
            border: none;
            border-radius: 17px;
            box-shadow: 7px 7px 11px #0000002B;
            min-width: 6rem !important;
            padding: 1.5rem 0;

            .dropdown-item {
                color: $primaryGray;
                font-size: 1.3rem;
            }

        }
    }
     }

     .sub-headings {
         font-weight: 600;
         
     }
     
 }

 // important note
 .note-section {
     max-width: 900px;
     padding: 20px 50px;
     box-shadow: 7px 7px 11px rgba(0, 0, 0, 0.17);
     border-radius: 10px;
     a {
      border-bottom: 1px solid transparent;
      transition: .2s ease;
        &:hover{
      border-color:$primary-red;
      color: $primary-red; 
}
     }
    }

 .form-section {
     h2 {
         font-weight: 400;
         
     }

     .form-template {
         box-shadow: 7px 7px 11px rgba(0, 0, 0, 0.17);
         border-radius: 10px;

         .template-text {
            border-radius: 10px 0 0 10px;
            .form-img {
                 max-width: 325px;
                 margin-top: 60px;
             }
             p{
                font-family: Poppins,sans-serif;
                font-size: 14px;
                font-weight: 500;
             }
         }

         .template-inputs {

             padding: 100px 15px 70px;

             //  form error ul
             .parsley-errors-list{
                 list-style-type: none;
                 padding: 8px 0 0 0;

                 li {
                     color: $primary-red;
                 }
             }
             .msg-error{
                color: $primary-red;
             }
             // error text-field background-color
             input.parsley-error,
             select.parsley-error,
             textarea.parsley-error {
                 background-color: #fce9e9;
                 border: 1px solid #EED3D7;
             }

             .form-control {
                 border-radius: 10px;
             }

             .resume {
                 display: block;
             }

             #custom-button {
                 border-radius: 10px;
                 border: 1px solid #ced4da;
                 width: 210px;
                 height: 38px;

                 &:focus {
                     outline: none;
                 }
             }

             .submit-btn {
                 text-align: right;

                 // margin-right: 60px;
                 button {
                     padding: 8px 34px;
                     margin: 0 -18px;
                 }

                 button:disabled {
                   cursor: no-drop;
                 }
             }
         }
     }
 }
