        // modal pop-up banner
        .popup-banner-modal {

          // display: block; 
          &.in {
            display: -webkit-box;
            display: flex;
            -webkit-box-pack: center;
            justify-content: center;
            -webkit-box-align: center;
            align-items: center;

          }

          .banner-image {
            border-radius: 4px;
          }

          .modal-lg {
            width: 100% !important;
            max-width: 80% !important;
          }

          .modal-body {
            padding: 0;
          }

          .close {
            position: absolute;
            top: -0.4em;
            right: -0.4em;
            opacity: 1;
            z-index: 3;
            text-shadow: none;
            color: #fff !important;
          }

        }

        //  banner youtube modal
        .youtube-modal {
          z-index: 1045;
        }

        .modal-content {
          background-color: transparent;
          border: none;
        }

        .youtube-modal {
          .close {
            position: absolute;
            top: 6px;
            right: 10px;
            z-index: 1046;
            font-size: 1em;
          }
        }

        // Banner setcion 
        // banner overlay 
        .banner::after {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background-color: rgba(0, 0, 0, 0.02);
          content: '';
          opacity: 0.98;
        }

        .bannerImage {
          position: absolute;
          left: 0;
          top: 0;
        }

        .bannerContent {
          z-index: 2;
          position: relative;
          padding-top: 11.56rem;
          max-width: 40.25rem;
          margin-bottom: 10%;

          .banner-title {
            display: inline;
            font-size: 4.375rem;
            line-height: 1.14;
            letter-spacing: 1.12px;
          }

          .banner-subtitle {
            max-width: 462px;
            margin: 30px 0;
            letter-spacing: 0.5px;
          }

          .btn-primary {

            // internal button spacing
            padding: 12px 22px;

            // video icon
            .far {
              font-size: 20px;
              padding: 0 3px;
              vertical-align: text-bottom;
            }
          }
        }

        // Rubico mission section
        .rubico-mission {
          padding-top: 8rem;

          h2 {
            max-width: 676px;
          }

          p {
            font-size: 1.56rem;
            line-height: 1.2;
            max-width: 568px;
            margin: -30px auto 20px;
            font-weight: 600;
            letter-spacing: 0.5px;
          }
        }

        // branches section
        .branches-section {

          .img-lg {
            max-height: 370px;
            height: 370px;
            padding-right: 0;
          }

          .img-sm>img:first-child {
            max-height: 205px;
            height: 205px;
            margin-bottom: 15px;
          }

          .img-sm>img:last-child {
            max-height: 150px;
            height: 150px;
          }
        }

        .branch-name {
          font-size: 1.9rem;
          margin-top: 12px;

        }

        .img-grid {
          margin: 15px -7.5px 0 -7.5px;

          >div {
            padding: 0 7.5px;

            img {
              max-height: 150px;
            }
          }



        }

        .haridwar-section,
        .dehradun-section {


          p {
            max-width: 400px;
            margin: 48px auto 16px auto;
          }
        }

        .haridwar-section {

          img {
            max-height: 205px;
          }
        }

        // Technologies section
        .technologies {
          box-shadow: 0px 3px 6px #00000029;

          .container {
            max-width: 992px;

            h2 {
              max-width: 560px;
            }
          }

          //  cards inside technologies section
          .card {
            max-width: 300px;
            margin-left: auto;
            margin-right: auto;
            border: none;
            border-radius: 17px;
            -webkit-transition: 0.2s linear;
            transition: 0.2s linear;

            &:hover {
              -webkit-transform: scale(1.08);
              transform: scale(1.08);

            }
          }

          .card-body {
            padding: 20px;
          }

          .card-title {
            color: #505D6A;
            font-weight: 600;
          }
        }

        //  Current Openings section
        .openings-section {
          h2 {
            max-width: 480px;
          }

          .openings-carousel {
            img {
              margin: 15px auto;
              height: 100px;
              object-fit: contain;
            }

            h3 {
              display: inline-block;
            }

            p {
              display: inline-block;
              margin-top: auto;

            }

            a {
              padding: 5px;

              div {
                border-color: transparent !important;
                -webkit-transition: 0.2s ease;
                transition: 0.2s ease;

                &:hover {
                  border-color: $primary-red !important;
                  color: $primary-red !important;

                }
              }
            }

            .slick-list {
              display: -webkit-box;
              display: flex;

              .slick-track {
                display: -webkit-box;
                display: flex;

                .slick-slide {
                  margin: 0 27px;
                  padding: 0 12px;

                  >div {
                    height: 100%;

                    >div {
                      height: 100%;

                      >a {
                        height: 100%;
                        display: -webkit-box !important;
                        display: flex !important;
                        -webkit-box-orient: vertical;
                        -webkit-box-direction: normal;
                        flex-direction: column;
                        -webkit-box-align: center;
                        align-items: center;
                        -webkit-box-pack: center;
                        justify-content: center;
                      }
                    }
                  }
                }
              }
            }

            // carousel button's content
            .slick-arrow {
              font-weight: 900;
              width: auto;
              height: auto;
              z-index: 1;

              &:before {
                font-family: Font Awesome\ 5 Free;
                font-size: 35px;
              }
            }

            // prev button
            .slick-prev {
              &:before {
                content: "\f053";
              }
            }

            // next button
            .slick-next {
              &:before {
                content: "\f054";
              }
            }
          }
        }

        //  Gallery section
        .gallery-carousel-section {
          position: relative;
          max-width: 810px;
          right: 1%;

          h2 {
            max-width: none;
          }

          img {
            max-width: 100%;
            height: 27.687rem !important;
          }

          // gallery carousel button's content 
          .slick-arrow {
            font-weight: 900;
            background-color: #fff;
            width: auto;
            height: auto;
            z-index: 1;
            margin-top: 238px;

            &:before {
              font-family: Font Awesome\ 5 Free;
              font-size: 28.96px;
            }
          }

          // prev button
          .slick-prev {
            box-shadow: 1px 3px 3px #00000029;
            left: auto;
            right: 0;
            padding: 5px 25px 5px 20px;

            &:before {
              content: "\f053";
            }
          }

          // next button
          .slick-next {
            box-shadow: 2px 3px 2px #00000029;
            padding: 5px 20px 5px 25px;
            right: -60.6px;

            &:before {
              content: "\f054";
            }
          }

          .label-gallery {
            bottom: 16%;
            left: -18%;
            padding: 10px 38px 10px 46px;
          }

          .label-organisation {
            top: 32%;
            line-height: 1.2;
            max-width: 270px;
            right: -19%;
            padding: 14px 38px 12px;
          }
        }

        // Testimonials section
        .testimonials-section {
          margin-top: 1.875rem;
          padding-bottom: 0;

          .testimonials-carousel {
            display: -webkit-box;
            display: flex;
            -webkit-box-align: center;
            align-items: center;

            img,
            p {
              margin: 45px auto;
            }

            h5,
            p {
              display: none;

            }

            h5 {
              font-weight: 600;
              max-width: 660px;
              margin: 80px auto 45px;
            }

            .slick-track {
              padding: 2.5rem 0 0;
            }

            .slick-slide {
              .overlay {
                position: relative;
                min-height: 28rem;

                img {
                  border-radius: 50%;
                  width: 130px;
                  height: 130px;
                  overflow: hidden;
                  -o-object-fit: cover;
                  object-fit: cover;
                  margin: 0 auto;
                  transition: all 0.5s ease;
                }

                &::after {
                  border-radius: 50%;
                  width: 8.12rem;
                  height: 8.12rem;
                  margin: 0 auto;
                  position: absolute;
                  content: '';
                  top: 0;
                  left: 0;
                  right: 0;
                  bottom: 0;
                  background-color: rgba(0, 0, 0, 0.5);
                }
              }
            }

            .slick-center {
              .overlay::after {
                background-color: transparent;
              }

              img {
                border: 3px solid $primary-red;
                -webkit-transform: scale(1.34);
                transform: scale(1.34);
              }

              h5,
              p,
              span {
                display: block;
              }
            }

            // carousel button's content
            .slick-arrow {
              font-weight: 900;
              width: auto;
              height: auto;
              z-index: 1;

              &:before {
                font-family: Font Awesome\ 5 Free;
                font-size: 3.75rem;
              }
            }

            // prev button
            .slick-prev {
              left: 10px;

              &:before {
                content: "\f053";
              }
            }

            // next button
            .slick-next {
              right: 10px;

              &:before {
                content: "\f054";
              }
            }
          }
        }

        // Blogs section
        .blogs-section {
          p {
            font-size: 16px;
          }

          h5 {
            font-weight: 600;
            margin: 1.25rem 0 3px 0;
          }

          .blog-img {
            box-shadow: 0px 3px 6px #00000029;
            height: 200px;
          }

          .about-author {
            .author-img {
              width: 35px;
              height: 35px;
              border-radius: 50%;
              overflow: hidden;
              border: 2px solid $primary-red;
              margin-right: 6px;
            }

            span {
              font-size: 14px;
            }
          }
        }
